@import url(https://cdn.jsdelivr.net/npm/reset-css@5.0.1/reset.min.css);
@import url(https://fonts.googleapis.com/css2?family=Nanum+Gothic&display=swap);
* {
  box-sizing: border-box;
}

input {
  all: unset;
  box-sizing: border-box;
  -webkit-appearance: none;
     -moz-appearance: none;
          appearance: none;
}

body {
  background-color: rgb(26, 26, 26);
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen,
    Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
  font-size: 14px;
  color: white;
}

button {
  background-color: white;
  color: black;
}

a {
  text-decoration: none;
  color: inherit;
}

form {
  width: 100%;
}

.nweet-input::-webkit-input-placeholder {
  color: white;
  opacity: 0.9;
}

.nweet-input:-ms-input-placeholder {
  color: white;
  opacity: 0.9;
}

.nweet-input::placeholder {
  color: white;
  opacity: 0.9;
}

.container {
  width: 100%;
  max-width: 320px;
  display: flex;
  flex-direction: column;
}

.formInput {
  width: 100%;
  padding: 10px 20px;
  border-radius: 20px;
  border: 1px solid black;
  text-align: center;
  background-color: white;
  color: black;
}

.formBtn {
  cursor: pointer;
  width: 100%;
  padding: 7px 20px;
  text-align: center;
  color: white;
  border-radius: 20px;
  background-color: #da89bc;
  cursor: pointer;
}

.cancelBtn {
  cursor: pointer;
  background-color: rgb(151, 176, 223);
}

/* Profile Page */

.profileForm {
  border-bottom: 1px solid rgba(255, 255, 255, 0.9);
  padding-bottom: 30px;
  width: 100%;
  display: flex;
  flex-direction: column;
}

.logOut {
  margin-top: 50px;
}

/* Nwitter Factory */

.factoryForm {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
}

.factoryInput__container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap;
  position: relative;
  margin-bottom: 20px;
  width: 100%;
}

.factoryInput__input {
  flex-grow: 1;
  height: 40px;
  padding: 0px 20px;
  color: white;
  border: 1px solid #e6c5da;
  border-radius: 20px;
  font-weight: 500;
  font-size: 12px;
}

.factoryInput__arrow {
  position: absolute;
  right: 0;
  background-color: #e6c5da;
  height: 40px;
  width: 40px;
  padding: 10px 0px;
  text-align: center;
  border-radius: 20px;
  color: white;
}

.factoryInput__label {
  color: #e6c5da;
  cursor: pointer;
}

.factoryInput__label span {
  margin-right: 10px;
  font-size: 12px;
}

.factoryForm__attachment {
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.factoryForm__attachment img {
  height: 80px;
  width: 80px;
  border-radius: 40px;
  background-color: #eee;
}

.factoryForm__clear {
  color: #e6c5da;
  cursor: pointer;
  text-align: center;
}

.factoryForm__clear span {
  margin-right: 10px;
  font-size: 12px;
}

/* Nweet */

.nweetEdit .formBtn {
  cursor: pointer;
  margin-top: 15px;
  margin-bottom: 5px;
}

.nweet {
  margin-bottom: 20px;
  background-color: white;
  width: 100%;
  /* height: auto; */
  max-width: 320px;
  padding: 20px;
  border-radius: 10px;
  position: relative;
  display: flex;
  flex-direction: column;
  color: rgba(0, 0, 0, 0.8);
  /* 자동 줄바꿈 */
  word-break:break-all; 
}

.nweet h4 {
  font-size: 14px;
}

.nweet .nweetTime {
  color: #999;
  font-size: 10px;
  margin-left: 10px;
}

.nweet .attachment {
  /* right: 40px; */
  /* position: absolute; */
  display: block;
  margin: auto;
  margin-top: 15px;
  border-radius: 5%;
  width: 95%;
  height: 120px;
  background-color: #eee;
  cursor: pointer;
}

.nweet__actions {
  position: absolute;
  right: 10px;
  top: 18px;
}

.nweet__actions span {
  cursor: pointer;
  color: #999;
  margin-right: 10px;
}

.nweet__actions span:hover {
  color: #e6c5da;
}

/* Auth */

.authContainer {
  display: flex;
  flex-direction: column;
  height: 100vh;
  justify-content: center;
  align-items: center;
}

.authBtns {
  display: flex;
  justify-content: space-between;
  width: 100%;
  max-width: 320px;
}

.authBtn {
  cursor: pointer;
  border-radius: 20px;
  border: none;
  padding: 10px 0px;
  font-size: 12px;
  text-align: center;
  width: 150px;
  background: white;
  cursor: pointer;
}

/* AuthForm */

.authInput {
  max-width: 320px;
  width: 100%;
  padding: 10px;
  border-radius: 30px;
  background-color: rgba(255, 255, 255, 1);
  margin-bottom: 10px;
  font-size: 12px;
  color: black;
}

.authSubmit {
  text-align: center;
  background: #e6c5da;
  color: white;
  margin-top: 10;
  cursor: pointer;
}

.authError {
  color: tomato;
  text-align: center;
  font-weight: 500;
  font-size: 12px;
}

.authSwitch {
  color: #e6c5da;
  cursor: pointer;
  margin-top: 10px;
  margin-bottom: 50px;
  display: block;
  font-size: 12px;
  text-decoration: underline;
}

.userName {
  color: #555;
  margin-bottom: 15px;
  font-size: bold 15px;
  font-family: 'Nanum Gothic', sans-serif;
}

.icon {
  margin-right: 6px;
}

#portal .modal{
  position: fixed;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  background: rgba(0, 0, 0, 0.7);
}

.nweet .modal {
  z-index: 1;
  width: auto;
  height: auto;
  background: white;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 5px;
  position: absolute;
  animation: animate 0.3s; 
  top: -120px;
}

.modal img {
  width: 100%;
  height: 100%;
  /* max-width: 80vh; */
   cursor: pointer;
 }

 .Xbutton {
  position: absolute;
  top: 7px;
  right: 7px;
  padding: 8px 11px;
  background: rgba(0,0,0,0);
  color: white;
  font-weight: bold;
  border: none;
  outline: none;
  border-radius: 2px;
  cursor: pointer;
 }

@keyframes animate {
  from {
    transform: scale(0.5);
  }
  to {
    transform: scale(1);
  }
}

.upBtn {
  position: fixed;
  width: 40px;
  height: 40px;
  bottom: 50px;
  right: 50px;
  cursor: pointer;
  transition: transform 2s;
}

.upBtn:hover {
  background-color: #da89bc;
}

.upBtn .arrows {
  font-size: 18px;
  position: relative;
  /* top: -6px; */
  text-decoration: none;
}

